.app{
   padding: 2rem 3rem 3rem;
   width: 400px;
   box-shadow: 0 3px 4px 3px rgba(0,0,0,0.1);
   margin: 3rem auto 0; 
   border-radius: 4px;
}

:global{
   .form-control{
      padding: 0.75rem 1rem;
      border-radius: 4px;
      border: 1px solid #bbb;
      background-color: #f8f8f8;
      font-size: 13pt;
   }
   .inline-block{
      display: inline-block;
   }
   .py-0{
      padding-top: 0; 
      padding-bottom: 0;
   }
   .m-0{
      margin: 0
   }
   .bg-none{
      background: transparent
   }
   .border-0{
      border: none
   }
}